// Image / Video nach Header

.wp-block-image.overlap-image
    width: 100%
    margin: auto
    position: relative
    z-index: 99
    transform: translateY(-100px)
    margin-bottom: -200px

    img
        display: block
        margin: auto
        width: 100%
        max-width: 1340px

.secondary a
    position: relative
    background-color: transparent
    padding: 0

.secondary a:after
    content: ""
    width: 150px
    height: 15px
    background: url("/app/plugins/custom-scripts54/src/assets/icons/arrow-small.svg")
